import { mapState } from "vuex";
import uploadFile from '../../../../core/upload.js';

export default {
    data() {
        return {
            tempFilePaths: '',
            imgheight: 0,
            menu: false,
            list: [],
            txt: '',
            textDialog: false,
            overflow: false,
            is_delete: false,
            toToggle: false,
            index: 0,
            reEdit: false,
            loadingImg: false,
            iphone_x: false
        }
    },
    computed: {
        ...mapState({
            userInfo: state => state.user.info,
            adminImg: state => state.mallConfig.__wxapp_img.app_admin
        })
    },
    methods: {
        toList() {
            this.textDialog = false;
            let item = {
                type:'text',
                text: this.txt
            };
            if(this.reEdit) {
                this.list.splice(this.index,1,item);
                this.reEdit = false;
            }else {
                this.list.splice(this.index,0,item);
            }
        },
        editText(index,item) {
            this.txt = item.text;
            this.reEdit = true;
            this.textDialog = true;
            this.index = index;
        },
        move(type,index,item) {
            this.list.splice(index,1);
            if(type == 1) {
                this.list.splice(index + 1,0,item);
            }else {
                this.list.splice(index - 1,0,item);
            }
        },

        toRemove(index) {
            this.txt = null;
            this.is_delete = true;
            this.index = index;
        },

        cancel() {
            this.is_delete = false;
        },

        remove() {
            this.is_delete = false;
            this.list.splice(this.index,1);
        },

        showMenu(index) {
            if(index > -1) {
                this.index = index + 1;
                this.menu = true;
                this.txt = '';
                this.toToggle = false;
            }else {
                if(this.txt.length > 0) {
                    this.toList();
                }else {
                    this.textDialog = false;
                }
                this.menu = false;
            }
        },

        toggle(index) {
            this.index = index;
            this.toToggle = true;
            this.menu = true;
        },

        addLine() {
            let item = {type:'line'}
            this.list.splice(this.index,0,item)
            this.menu = false;
        },

        showText() {
            this.textDialog = !this.textDialog;
            this.menu = false;
        },

        add(sourceType) {
            let that = this;
            this.menu = false;

            uni.chooseImage({
                count: 1,
                sizeType: ['original', 'compressed'],
                sourceType: [sourceType],
                success(res) {
                    // tempFilePath可以作为img标签的src属性显示图片
                    const tempFilePaths = res.tempFilePaths;
                    let list = that.list;
                    tempFilePaths.forEach(function (row, index) {
                        let para = {
                            pic_url: '',
                            type: 'img',
                            id: 0
                        };
                        that.loadingImg = true;
                        that.toToggle ? list.splice(that.index,1,para) : list.splice(that.index,0,para);
                        that.list = list;
                        let fileName = '';



                        uploadFile({
                            url: that.$api.upload.file,
                            filePath: row,
                            fileType: 'image',
                            mch_id: 0,
                            fileName: fileName
                        }).then(res => {
                            let imgInfo = undefined;


                            imgInfo = JSON.parse(res.data);






                            if (imgInfo.code == 0) {
                                for(let i in list) {
                                    if(list[i].type === 'img' && list[i].pic_url === '') {
                                        list[i].pic_url = imgInfo.data.url;
                                        list[i].id = imgInfo.data.id;
                                    }
                                }
                            }
                            that.list = list;

                            if (index == tempFilePaths.length - 1) {
                                that.loadingImg = false;
                                uni.hideLoading();
                            }
                        });
                    })
                }
            })



































































        },
        imageLoad(index,e) {
            let $height = e.detail.height; //获取图片真实高度
            let $width = e.detail.width; //获取图片真实宽度
            let height = $height * (750 / $width);
            this.list[index].height = height + 'rpx';
            this.$forceUpdate();
        },
        clear(index) {
            this.img.splice(index,1)
            this.$storage.setStorageSync('goods_detail', this.list);
        },
        save() {
            if(!this.loadingImg) {
                this.$storage.setStorageSync('goods_detail', this.list);
                setTimeout(function() {
                    uni.navigateBack();
                }, 500)
            }else {
                uni.showToast({
                    title: '上传图片中，请稍后...',
                    icon: 'none',
                    duration: 1000
                });
            }
        }
    },

    onLoad() { this.$commonLoad.onload();
        let that = this;
        this.list = this.$storage.getStorageSync('goods_detail') ? this.$storage.getStorageSync('goods_detail') : [];
        uni.getSystemInfo({
            success: function (res) {
                if(res.model.indexOf('iPhone X') > -1 || res.model.indexOf('iPhone 11') > -1 || res.model.indexOf('iPhone11') > -1 || res.model.indexOf('iPhone12') > -1 || res.model.indexOf('Unknown Device') > -1) {
                    that.iphone_x = true;
                }
            }
        })
    }
}
